.title {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  line-height: 27px;
  font-weight: 800;
  font-family: inherit;
}
.buttonCloseIcon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  cursor: pointer;
}
.header {
  align-self: stretch;
  border-bottom: 7px solid var(--color-gray-200);
  box-sizing: border-box;
  height: 90px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-xs) var(--padding-xl) var(--padding-mini);
  align-items: center;
  justify-content: space-between;
}
.image6Icon,
.image8Icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 0;
}
.image8Icon {
  z-index: 1;
}
.componentImgChild {
  position: absolute;
  margin: 0 !important;
  top: calc(50% - 103px);
  left: calc(50% - 0px);
  background-color: var(--color-gray-500);
  width: 180px;
  height: 206px;
  display: none;
  z-index: 2;
}
.componentImg,
.componentImgWeb {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.componentImg {
  flex: 1;
  justify-content: flex-end;
  position: relative;
}
.componentImgWeb {
  height: auto;
  justify-content: flex-start;
  aspect-ratio: 6;
}
.selectingFootrestFitting {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: 1px;
  line-height: 40px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: inherit;
  display: flex;
  align-items: center;
  width: 114px;
  flex-shrink: 0;
}
.titleOption11 {
  align-self: stretch;
  background-color: var(--basic-white);
  display: flex;
  flex-direction: row;
  padding: var(--padding-8xs-5) var(--padding-253xl) var(--padding-7xs-5)
    var(--padding-mini);
  align-items: flex-start;
  justify-content: flex-start;
}
.basic1 {
  position: relative;
  letter-spacing: 1px;
  line-height: 40px;
  text-transform: uppercase;
}
.discIcon {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.optionButton {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.basic,
.optionButton {
  align-self: stretch;
}
.basic,
.comfortable,
.options {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-11xs);
}
.options {
  flex: 1;
  background-color: var(--basic-white);
  padding: var(--padding-8xs-5) var(--padding-mini) var(--padding-7xs-5)
    var(--padding-lg);
  justify-content: flex-start;
  gap: var(--gap-31xl);
}
.options01 {
  align-self: stretch;
  flex-direction: row;
  font-size: var(--font-size-base);
}
.div,
.options01,
.titleOption1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.titleOption1 {
  flex: 1;
  flex-direction: column;
  padding: var(--padding-0) var(--padding-xl) var(--padding-xl)
    var(--padding-41xl);
  gap: var(--gap-3xs);
}
.div {
  align-self: stretch;
  background-color: var(--basic-white);
  border-bottom: 1px solid var(--color-gainsboro);
  flex-direction: row;
}
.selectingShoeCushion {
  position: relative;
  letter-spacing: 1px;
  line-height: 40px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  width: 75px;
  flex-shrink: 0;
}
.div1,
.options02 {
  align-self: stretch;
  background-color: var(--basic-white);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.options02 {
  padding: var(--padding-8xs-5) var(--padding-mini) var(--padding-7xs-5)
    var(--padding-lg);
  align-items: center;
  gap: var(--gap-31xl);
  font-size: var(--font-size-base);
  font-family: var(--font-poppins);
}
.div1 {
  border-bottom: 1px solid var(--color-gainsboro);
  align-items: flex-start;
  font-family: var(--font-inter);
}
.lettering {
  position: relative;
  letter-spacing: 1px;
  line-height: 40px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  width: 52px;
  flex-shrink: 0;
}
.optionButton6 {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
}
.inputLettering,
.optionButton6,
.options03 {
  display: flex;
  justify-content: flex-start;
}
.inputLettering {
  background-color: var(--basic-white);
  width: 348px;
  height: 50px;
  flex-direction: row;
  padding: var(--padding-8xs-5) var(--padding-mini) var(--padding-7xs-5)
    var(--padding-lg);
  box-sizing: border-box;
  align-items: center;
}
.options03 {
  align-self: stretch;
  flex-direction: column;
  align-items: flex-start;
  font-size: var(--font-size-base);
  font-family: var(--font-poppins);
}
.order {
  position: relative;
  letter-spacing: 0.9px;
  line-height: 35px;
  text-transform: uppercase;
}
.orderButoon {
  align-self: stretch;
  height: 76px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-0) var(--padding-41xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-silver);
}
.customOptions {
  align-self: stretch;
  background-color: var(--basic-white);
  align-items: flex-start;
}
.customOptions,
.divContainer,
.mainWeb {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.divContainer {
  align-self: stretch;
  align-items: flex-start;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-gray-300);
}
.mainWeb {
  position: absolute;
  width: calc(100% + 2px);
  top: -1px;
  right: -1px;
  left: -1px;
  background-color: var(--basic-white);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  overflow: hidden;
  align-items: center;
  gap: var(--gap-base);
}
.option3 {
  position: relative;
  width: 100%;
  height: 1390px;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 570px) {
  .title {
    font-size: var(--font-size-lg);
  }
  .basic1,
  .selectingFootrestFitting {
    font-size: var(--font-size-xs);
  }
  .options {
    gap: var(--gap-2xl);
  }
  .titleOption1 {
    gap: var(--gap-0);
    padding-left: var(--padding-0);
    padding-bottom: var(--padding-0);
    box-sizing: border-box;
  }
  .selectingShoeCushion {
    font-size: var(--font-size-xs);
  }
  .options02 {
    gap: var(--gap-xl);
  }
  .lettering {
    font-size: var(--font-size-xs);
  }
  .order {
    font-size: var(--font-size-lg);
  }
  .orderButoon {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
