@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Arial:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DIN+Condensed:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;800&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --button-text: "Noto Sans KR";
  --font-arial: Arial;
  --font-inter: Inter;
  --font-din-condensed: "DIN Condensed";
  --font-poppins: Poppins;

  /* font sizes */
  --button-text-size: 14px;
  --font-size-base: 16px;
  --font-size-mini-4: 14.4px;
  --font-size-lg: 18px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --font-size-xs-2: 11.2px;

  /* Colors */
  --basic-white: #fff;
  --color-crimson: #e6003b;
  --color-darkgray: #a5a5a5;
  --color-black: #000;
  --color-gray-100: #777;
  --color-gray-200: #222;
  --color-gray-300: #1b1b1b;
  --border-normal: #111;
  --color-gray-400: rgba(0, 0, 0, 0.87);
  --color-gray-500: rgba(255, 255, 255, 0.9);
  --text-hint: #cfcfcf;
  --color-lightgray-100: #cacaca;
  --color-silver: #bdbdbd;
  --color-gainsboro: #ddd;
  --color-whitesmoke: #eaeaea;

  /* Gaps */
  --gap-mini: 15px;
  --gap-6xs: 7px;
  --gap-base: 16px;
  --gap-11xs: 2px;
  --gap-xl: 20px;
  --gap-2xl: 21px;
  --gap-3xs: 10px;
  --gap-0: 0px;
  --gap-31xl: 50px;

  /* Paddings */
  --padding-8xs: 5px;
  --padding-5xs: 8px;
  --padding-0: 0px;
  --padding-12xs-7: 0.7px;
  --padding-10xs-5: 2.5px;
  --padding-10xs: 3px;
  --padding-12xs: 1px;
  --padding-base: 16px;
  --padding-8xs-5: 4.5px;
  --padding-mini: 15px;
  --padding-7xs-5: 5.5px;
  --padding-lg: 18px;
  --padding-xl: 20px;
  --padding-41xl: 60px;
  --padding-253xl: 272px;
  --padding-xs: 12px;
  --padding-12xs-5: 0.5px;
  --padding-smi-4: 12.4px;
  --padding-2xs: 11px;
  --padding-3xs-6: 9.6px;
  --padding-5xs-8: 7.8px;

  /* border radiuses */
  --br-9xs: 4px;
}
